import React, { Component } from 'react';
import './index.scss';

export default class Licenses extends Component {
    constructor(props) {
        super(props);
        this.state = {
            dataList: Array(50).fill('')
        };
    }

    componentDidMount() {
        this.props.changeType('licenses');
    }

    render() {
        return (<div className="dashboard-licenses-page">
            <table>
                <thead>
                    <tr>
                        <th>LICENSE</th>
                        <th>EMAIL</th>
                        <th>STATUS</th>
                        <th>PLAN</th>
                        <th>USER</th>
                        <th>CREATED</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {this.state.dataList.map((item, index) => <tr key={index}>
                        <td>JOKER-1239-8492-0332-3233</td>
                        <td>piahoffg340@gmail.com</td>
                        <td>Active</td>
                        <td>25 / Month</td>
                        <td>alexheh94#001</td>
                        <td>June1, 5:35 AM</td>
                        <td></td>
                    </tr>)}
                </tbody>
            </table>
        </div>);
    }
}